import React, { useState, useEffect, useCallback } from 'react';
import { Link, useLocation } from 'react-router-dom';
// import * as EgovNet from '../../api/egovFetch'; //   
import URL from '../../constants/url'; //   
// import { set } from 'msw/lib/types/context'

function EgovMain(props) {
    console.group('EgovMain');
    console.log('[Start] EgovMain ------------------------------');
    console.log('EgovMain [props] : ', props);

    const location = useLocation();
    console.log('EgovMain [location] : ', location);

    const [noticeListTag, setNoticeListTag] = useState();
    const [gallaryListTag, setGallaryListTag] = useState();
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    const retrieveList = useCallback(() => {
        fetch('/mainPage') // mainPage  .
            .then((res) => res.json()) //  json  .
            .then((data) => {
                const notiList = data.result.notiList || []; //  
                const galList = data.result.galList || []; //  

                const mutNotiListTag = notiList.length
                    ? notiList.map((item) => (
                          <li key={item.nttId}>
                              <Link
                                  to={{ pathname: URL.INFORM_NOTICE_DETAIL }}
                                  state={{ nttId: item.nttId, bbsId: item.bbsId }}
                              >
                                  {item.nttSj}
                                  <span>{item.frstRegisterPnttm}</span>
                              </Link>
                          </li>
                      ))
                    : [<li key="0">  .</li>];

                const mutGallaryListTag = galList.length
                    ? galList.map((item) => (
                          <li key={item.nttId}>
                              <Link
                                  to={{ pathname: URL.INFORM_GALLERY_DETAIL }}
                                  state={{ nttId: item.nttId, bbsId: item.bbsId }}
                              >
                                  {item.nttSj}
                                  <span>{item.frstRegisterPnttm}</span>
                              </Link>
                          </li>
                      ))
                    : [<li key="0">  .</li>];

                setNoticeListTag(mutNotiListTag);
                setGallaryListTag(mutGallaryListTag);
                setLoading(false);
            })
            .catch((err) => {
                setError(err);
                setLoading(false);
            });
    }, []);

    useEffect(() => {
        retrieveList();
    }, [retrieveList]);

    if (loading) return <div> ...</div>;
    if (error) return <div> : {error.message}</div>;

    return (
        <div className="container P_MAIN">
            <div className="c_wrap">
                <div className="colbox">
                    <div className="left_col">
                        <img
                            src="/assets/images/img_simple_main.png"
                            alt="           ."
                        />
                    </div>

                    <div className="right_col">
                        <div className="mini_board">
                            <ul className="tab">
                                <li>
                                    <a href="#" className="on">
                                        
                                    </a>
                                </li>
                                <li>
                                    <a href="#"></a>
                                </li>
                            </ul>
                            <div className="list">
                                <div className="notice">
                                    <h2 className="blind"></h2>
                                    <ul>{noticeListTag}</ul>
                                    <Link to={URL.INFORM_NOTICE} className="more">
                                        
                                    </Link>
                                </div>

                                <div className="gallary">
                                    <h2 className="blind"></h2>
                                    <ul>{gallaryListTag}</ul>
                                    <Link to={URL.INFORM_GALLERY} className="more">
                                        
                                    </Link>
                                </div>
                            </div>
                        </div>

                        <div className="banner">
                            <Link to={URL.SUPPORT_DOWNLOAD} className="bn1">
                                <strong></strong>
                                <span>
                                     
                                    <br />
                                       .
                                </span>
                            </Link>
                            <Link to={URL.ABOUT} className="bn2">
                                <strong></strong>
                                <span>
                                    
                                    <br />
                                       .
                                </span>
                            </Link>
                        </div>
                    </div>
                </div>

                <div className="banner_bot">
                    <div className="b1">
                        <div>
                            <h2> </h2>
                            <p>
                                 
                                <br />
                                  .
                            </p>
                        </div>
                        <Link to={URL.INTRO_WORKS}> </Link>
                    </div>
                    <div className="b2">
                        <div>
                            <h2> </h2>
                            <p>
                                 
                                <br />
                                  
                                <br />
                                .
                            </p>
                        </div>
                        <Link to={URL.INTRO_SERVICE}> </Link>
                    </div>
                    <div className="b3">
                        <div>
                            <h2> </h2>
                            <p>
                                 
                                <br />
                                  
                                <br />
                                   .
                            </p>
                        </div>
                        <Link to={URL.SUPPORT_APPLY}> </Link>
                    </div>
                    <div className="b4">
                        <div>
                            <h2> </h2>
                            <p>
                                 
                                <br />
                                  
                                <br />
                                   .
                            </p>
                        </div>
                        <Link to={URL.INFORM}> </Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default EgovMain;
